import { useEffect } from 'react';

import { fetchGlasses, useCollectionContext } from 'src/modules/store/collection';
import { useCollectionCategoriesContext, getCategories } from 'src/modules/store/collectionCategories';

import Filters from './Filters';
import CurrentFilter from './Filters/CurrentFilter';
import Gallery from './Gallery';

import styles from './CollectionPage.module.scss';

const DEFAULT_COLLECTION_NAME = 'Spectacles Women';

const CollectionPage = () => {
  const { state: { collectionId, colourFilter, frameFilter }, dispatch: collectionDispatch } = useCollectionContext();
  const { state: categoriesState } = useCollectionCategoriesContext();
  const category = getCategories(categoriesState).find((category) => category.configurationName === collectionId)

  useEffect(() => {
    collectionDispatch && collectionDispatch(fetchGlasses(true));
  }, [collectionDispatch]);

  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <div className={styles.headerEmpty} />
        <div className={styles.headerTitle}>{category ? category.name : DEFAULT_COLLECTION_NAME}</div>
        <div className={styles.headerFilters}><Filters /></div>
      </header>
      {!!(Object.keys(colourFilter).length || Object.keys(frameFilter).length) && <CurrentFilter />}
      <div className={styles.gallery}>
        <Gallery />
      </div>
    </div>
  );
}

export default CollectionPage;
