import { fetchJson } from "./fetch";

const API_URL = 'https://staging-api.bloobloom.com/user/v1/sales_channels/website/collections';

export interface CollectionCategory {
  id: number,
  name: string,
  configurationName: string
}

interface CollectionCategoriesResponse {
  collections: Array<CollectionCategory>,
  meta: {
    totalCount: number
  }
}

const fetchCollectionCategories = () : Promise<CollectionCategoriesResponse> => fetchJson(API_URL);

export default fetchCollectionCategories;
