import { useEffect, useState, useRef, MouseEvent, UIEvent } from 'react';
import cn from 'classnames';

import {
  useCollectionCategoriesContext,
  getCategorizedCollections
} from 'src/modules/store/collectionCategories';
import handleButtonKeys from 'src/utils/handleButtonKeys';

import {
  useCollectionContext,
  changeCollectionId,
} from 'src/modules/store/collection';


import arrow from './arrow.svg';
import arrowWhite from './arrowWhite.svg';
import styles from '../Menu.module.scss';

// because user can sometimes move mouse out of the edges, and also move between elements, we should
// avoid jumping (quick trying close menu then quick re-open). For that, we start closing
// it by a small timeout
const SIDE_MENU_CLOSE_TIMEOUT = 100;

const MENU_BAR_HEIGHT = 48;

const SideMenu = ({ onMenuClicked, forceClose, onForceClose }:
                    {onMenuClicked: Function, forceClose: boolean, onForceClose: Function}) => {
  const { state } = useCollectionCategoriesContext();
  const { state: collectionState, dispatch: collectionDispatch } = useCollectionContext();
  const categories = getCategorizedCollections(state);

  const [openedMenuId, setOpenedMenuId] = useState(-1);
  const [isClickOpened, setIsClickOpened] = useState(false);
  const [isMenuOpened, setIsMenuOpened] = useState(false);
  const [isMenuFocused, setIsMenuFocused] = useState(false);
  const [isMenuHovered, setIsMenuHovered] = useState(false);
  const [isMenuClicked, setIsMenuClicked] = useState(false);

  const hideTimer = useRef<number>();

  const handleMenuItemClick = (event: MouseEvent, menuId: number) => {
    const mq = window.matchMedia( "(max-width: 719.9px)" );
    const isMobile = mq.matches;
    if (!isMobile) {
      // this will work just by focus when not mobile
      return;
    }

    setOpenedMenuId(() => menuId);
    setIsClickOpened((currentIsClickOpened) => !currentIsClickOpened);
    event.preventDefault();
    event.stopPropagation();
    document.activeElement instanceof HTMLElement && document.activeElement.blur();
  }

  const handleSubMenuOpen = (menuId: number) => {
    setOpenedMenuId(() => menuId);
    setIsMenuHovered(() => true);
  }

  const handleSubMenuOut = () => setIsMenuHovered(() => false);

  const handleSubMenuFocus = (menuId: number) => {
    setOpenedMenuId(() => menuId);
    setIsMenuFocused(() => true);
  }

  const handleSubMenuBlur = () => setIsMenuFocused(() => false);

  const handleNavigate = (page: string) => {
    // gracefully close menu when user choose menu item
    setIsMenuOpened(() => false);
    setIsClickOpened(() => false);
    setIsMenuClicked(() => true);
    onMenuClicked();

    // TODO: vgrynch: below should do navigate to appropriate collections route
    // alert(page);
    return page;
  }

  const updateCollection = (collectionId: string) => {
    if (collectionState.collectionId !== collectionId) {
      collectionDispatch && collectionDispatch(changeCollectionId(collectionId));
    }
  }

  const hide = () => {
    // force hiding menu when user choose menu item
    if (isMenuOpened || isClickOpened) {
      setIsMenuClicked(() => true);
    }
    setIsMenuOpened(() => false);
    setIsClickOpened(() => false);
  }

  const handleForceClose = () => {
    hide();
    onForceClose();
  }

  const handleSubMenuClick = (event: UIEvent, category: string, sub: string) => {
    document.activeElement instanceof HTMLElement && document.activeElement.blur();
    event.preventDefault();
    event.stopPropagation();

    hide();

    onMenuClicked();

    updateCollection(`${category}-${sub}`);
  }

  const mq = window.matchMedia( "(max-width: 719.9px)" );
  const isMobile = mq.matches;

  useEffect(() => {
    if (window) {
      window.addEventListener('mousedown', handleForceClose);

      return () => window.removeEventListener('mousedown', handleForceClose);
    }
  }, [])

  useEffect(
    () => {
      if (isMenuClicked) {
        setIsMenuClicked(() => false);
      } else if (forceClose && isMobile) {
        hide();
      } else if (isMenuHovered || isMenuFocused) {
        setIsMenuOpened(() => true)
        if (hideTimer.current) {
          clearTimeout(hideTimer.current);
          hideTimer.current = 0;
        }
      } else if (!hideTimer.current) {
        if (window) {
          hideTimer.current =
            window.setTimeout(() => setIsMenuOpened(() => false), SIDE_MENU_CLOSE_TIMEOUT);
        } else {
          setIsMenuOpened(() => false);
        }
      }

    },
    [isMenuHovered, isMenuFocused]
  );

  return (
    <>
      <div className={styles.side}>
        {categories.map(
          ({ id, name, configurationName, subcategories }, index) => (
            <div
              className={cn(styles.sideItem, {
                [styles.sideItemHover]: ((isClickOpened || isMenuOpened) && openedMenuId === id)
              })}
              key={id}
              role="menuitem"
              tabIndex={200 + index * 10 + 1}
              aria-label={`Open side submenu for ${name}`}
              aria-haspopup="menu"
              aria-expanded={(isClickOpened || isMenuOpened) && openedMenuId === id}
              onMouseDown={(event) => event.stopPropagation()}
              onClick={(event) => handleMenuItemClick(event, id)}
              onMouseOver={() => handleSubMenuOpen(id)}
              onMouseOut={handleSubMenuOut}
              onFocus={() => handleSubMenuFocus(id)}
              onBlur={handleSubMenuBlur}
            >
              {/* TODO: vgrynch: handle click for responsive only!!! */}
              <span className={styles.sideItemTitle}>{name}</span>
              <img
                src={(isClickOpened || isMenuOpened) && openedMenuId === id ? arrowWhite : arrow}
                alt="arrow"
              />
            </div>
          )
        )}
        <div
          className={styles.sideItem}
          role="menuitem"
          tabIndex={300}
          onClick={() => handleNavigate('home-try-on')}
          onKeyDown={(event) => handleButtonKeys(event, () => handleNavigate('home-try-on'))}
        >
          <span className={styles.sideItemTitle}>Home Try On</span>
        </div>
        <div
          className={styles.sideItem}
          role="menuitem"
          tabIndex={300}
          onClick={() => handleNavigate('stores')}
          onKeyDown={(event) => handleButtonKeys(event, () => handleNavigate('stores'))}
        >
          <span className={styles.sideItemTitle}>Free eye test</span>
        </div>
      </div>
      {categories.map(
        ({ id, name, configurationName, subcategories }, index) => (
          <div
            key={id}
            className={cn(styles.subContainer, {
              [styles.subOpen]: (isClickOpened || isMenuOpened) && openedMenuId === id,
              [styles.subClose]: !((isClickOpened || isMenuOpened) && openedMenuId === id)
            })}
            style={{
              top: `${(isMobile ? 0 : index * MENU_BAR_HEIGHT) - 1}px`,
              height: `calc(100vh - ${((isMobile ? 0 : index) + 1) * MENU_BAR_HEIGHT - 1}px)` }}
            role="menu"
            onMouseOver={() => handleSubMenuOpen(id)}
            onMouseOut={handleSubMenuOut}
            onFocus={() => handleSubMenuFocus(id)}
            onBlur={handleSubMenuBlur}
          >
            {subcategories.map((subcategory) => (
              <div
                role="button"
                tabIndex={-1}
                className={styles.subItem}
                key={subcategory.configurationName}
                onClick={(event) => handleSubMenuClick(event, configurationName, subcategory.configurationName)}
              >
                <a
                  role="menuitem"
                  className={styles.subItemTitle}
                  tabIndex={200 + index * 10 + 1}
                  onClick={(event) => handleSubMenuClick(event, configurationName, subcategory.configurationName)}
                  onKeyDown={(event) =>
                    handleButtonKeys(event, () =>
                      handleSubMenuClick(event, configurationName, subcategory.configurationName))}
                >{subcategory.name}</a>
              </div>
            ))}
          </div>
        )
      )}
    </>
  );
}

export default SideMenu;
