/**
 * Production-level fetching template that is consistent across browsers and APIs
  */
import fetch from 'isomorphic-fetch';
import { camelCase } from 'lodash';

export type UrlQueryParams = {
  [key: string]: string | number | boolean | Array<string | number | boolean>
};

type ObjectJsonNode = {
  [key: string]: JsonNode
}
type JsonNode = ObjectJsonNode | Array<JsonNode> | Object | string | number | boolean;

const camelCaseResponse = (obj: JsonNode): JsonNode => {
  if (Array.isArray(obj)) {
    return obj.map(v => camelCaseResponse(v));
  } else if (obj != null && obj.constructor === Object) {
    return Object.keys(obj).reduce(
      (result, key) => ({
        ...result,
        [camelCase(key)]: camelCaseResponse((obj as ObjectJsonNode)[key]),
      }),
      {},
    );
  }
  return obj;
}

const prepareParams = (url: string, params: UrlQueryParams) => {
  const query = Object.entries(params || {} as UrlQueryParams)
    .reduce((result: Array<string>, [key, value]) => (
      (!value && value !== false)
        ? result
        : [
            ...result,
            ...(
              Array.isArray(value) ?
                value.map((val) => `${encodeURIComponent(key)
                  .replace('%5D', ']')
                  .replace('%5B', '[')
                }=${encodeURIComponent(val)}`) :
                [`${encodeURIComponent(key)
                  .replace('%5D', ']')
                  .replace('%5B', '[')
                }=${encodeURIComponent(value)}`]
            )
          ]
      ), [])
    .join('&');

  return query ? `${url}?${query}` : url;
};

const validateResponse = (response : Response): Promise<any> => {
  if (response.ok) {
    return response.json();
  }

  if (response.status === 204) {
    return Promise.resolve();
  }

  // TODO: vgrynch: add api errors handling and converting into the exception - depends on API
  if (response.status >= 500) {
    throw { status: response.status, errors: 'Server API error' };
  }

  throw { status: response.status, errors: 'Unhandled API error' };
};

export const fetchJson = (url: string, params?: UrlQueryParams): Promise<any> => {
  const path = params ? prepareParams(url, params) : url;

  const headers = {
    // TODO: vgrynch: add session token sending here if need to
    'Content-Type': 'application/json',
    Cache: 'no-cache'
  };

  return fetch(path, {
    // credentials: 'include', // TODO: vgrynch: if we have a cross-site API URLs, APIs must provide headers to use "include" here
    method: 'GET',
    headers
  })
    .then(validateResponse)
    // TODO: v grynch: figure out if this is needed. Not all projects have a restriction on usage
    //  of camelcase in the code for data.
    .then(camelCaseResponse);
};
